import {ajax} from '../../common/js/util.js'
import toast from '../../common/js/toast.js'
import global_ from '../../common/js/global.js'
export default {
	data() {
		return {
			show: false,
			radio: 0,
			user: '',
			card: '',
			tel: '',
			address: '',
			content: '',
			location: '',
			imageData: [],
			list: {},
			url: global_.baseUrl
		}
	},
	onLoad(option) {
		this.init(option.id)
	},
	methods: {
		init(id) {
			let self = this
			let datas = {
				id: id
			}
			toast.showLoading('加载中...')
			let data = {
				url: '/appealGover/detail',
				data: datas,
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: function(res) {
					console.log(res)
					if (res.data.success == true) {
						toast.hideLoading()
						self.list = res.data.body
						if(self.list.anonymous == 1){
							self.list.cname = '****'
						}
						self.imageData = res.data.body.material
						self.imageData = self.imageData.split('|')
					}
				}
			}
			ajax(data)
		}
	}
}
